.blogger-profile {
  &__container {
    display: flex;
    justify-content: space-between;
    @include adaptive($mobile) {
      flex-direction: column;
      align-items: center; } }

  &__h1 {
    margin-bottom: 5px;
    margin-top: 0; }

  &__h2 {
    margin-top: 0; }

  &__bio {
    width: 100%;
    max-width: 594px;
    display: flex;
    flex-direction: column;
    @include adaptive($mobile) {
      max-width: 100%; } }

  &__subscribe-stat {
    margin-top: auto; }

  &__content-types {
    margin-top: auto;
    @include adaptive($mobile) {
      margin-bottom: 40px; } }

  &__avatar-callback {
    width: 100%;
    max-width: 758px;
    display: flex;
    flex-direction: column;
    align-items: center;
    @include adaptive($mobile) {
      order: 2; } }

  &__avatar {
    width: 100%;
    max-width: 758px;
    height: 720px;
    overflow: hidden;
    @include adaptive($mobile) {
      max-width: 300px;
      height: 300px; }

    img {
      height: 100%;
      object-fit: contain; } }

  &__callback {
    width: 234px; }

  &__socials {
    @include adaptive($mobile) {
      display: flex;
      flex-wrap: wrap; }
    li {
      margin-bottom: 25px;
      @include adaptive($mobile) {
        margin-bottom: 10px; } } } }




