.bloggers {
  overflow-x: hidden;
  padding-top: 20px; }
.blogger-card {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  width: 100%;
  max-width: 421px;
  height: 750px;
  padding: 20px 0;
  border-radius: 20px;
  background: $white;
  filter: drop-shadow(0px 8px 15px rgba(0, 44, 131, 0.1));
  &__top {
    display: flex;
    align-items: center;
    padding: 0 21px;
    margin-bottom: 33px;
    .avatar {
      width: 75px;
      height: 75px;
      padding: 5px;
      background-image: url(../img/wallpapers/circle.png);
      background-size: cover;
      background-repeat: no-repeat;
      margin-right: 21px;
      img {
        height: 100%;
        object-fit: cover;
        border-radius: 100%; } }
    .name {
      max-width: calc(100% - 100px);
      font-style: normal;
      font-weight: 700;
      font-size: 22px;
      line-height: 28px;
      color: $black-dark; } }
  &__cover {
    height: 241px;
    margin-bottom: 22px;
    overflow: hidden; }
  &__text {
    padding: 0 21px;
    font-size: 18px;
    line-height: 23px;
    color: $grey-dark; }
  &__soc {
    margin-top: auto;
    padding: 0 21px;
    .title {
      font-weight: 600;
      font-size: 18px;
      line-height: 23px;
      color: $black;
      margin-bottom: 16px; }
    ul {
      display: flex;
      flex-wrap: wrap;
      li {
        width: 45%;
        display: flex;
        align-items: center;
        justify-content: flex-start;
        a {
          display: flex;
          align-items: center;
          justify-content: flex-start;
          font-weight: 500;
          font-size: 18px;
          line-height: 23px;
          color: $black-dark; } } } }
  &__button {
    margin: auto;
    margin-bottom: 0; } }
